.main {
	position: relative;
	// padding: 225px 0 0 40px
	// padding-bottom: 40px
	margin: 0 auto;
	max-width: 1680px;
	z-index: 101;
	background-color: $white;
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 50%;
		width: 100vw;
		margin-left: -50vw;
		height: 100%;
		background-color: $white; }
	@include lgd {
		// padding: 100px 0 0 20px
		padding-left: 20px; }
	@include mdd {
		// padding: 36px 0 0
		padding-left: 0;
		padding-right: 0; }
	// +lg
	// 	padding: 100px 0 0 20px
	// +md
	// 	padding: 80px 0 0
	&__inner {
		position: relative;
		// padding: 21px 0 0 380px
		padding-left: 380px;
		@include lgd {
			padding: 0 0 0 340px; }
		@include mdd {
			padding-left: 0; } } }
